.page-header {
  padding: 10px 0;
}

.page-header_nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.site-menu_list {
  @include reset-list;

  display: flex;
  flex-direction: row;

  text-transform: uppercase;
}

.navbar-toggler-icon {
  background-image: url("../img/icon-menu.png");
  background-repeat: no-repeat;
  background-position: center;

  &:hover {
    opacity: 0.7;
  }
}

.navbar-collapse {
  display: flex;
  justify-content: flex-end;
  width: auto;
}

.site-menu_item {
  margin-right: 40px;

  font-size: 14px;

  &:last-child {
    margin-right: 0;
  }
}

.page-header_img {
  &:hover {
    opacity: 0.7;
  }
}

.site-menu_link {
  font-size: 13px;
  color: $basic-black;
  text-decoration: none;

  cursor: pointer;

  &:hover {
    color: $basic-black;

    opacity: 0.6;
  }

  &:focus {
    outline: none;
    opacity: 0.6;
  }
}

.phones {
  @include reset-list;

  margin-left: 10%;
}

.phones_item {
  &:last-child {
    margin-top: 5px;
  }
}

.phones_link {
  font-weight: 600;
  color: $basic-blue;
  text-decoration: none;

  &:hover {
    color: $basic-grey;

    opacity: 0.6;
  }

  &:focus {
    outline: none;
    opacity: 0.6;
  }
}

.phones_link-btn {
  padding: 5px 10px;

  font-weight: 400;
  font-size: 16px;
  color: $basic-white;

  background-color: $basic-blue;
  border-radius: 20px;

  &:hover {
    color: $basic-white;

    opacity: 0.6;
  }

  &:focus {
    outline: none;
    opacity: 0.6;
  }
}


@media screen and (max-width: 991px) {
  .page-header {
    flex-wrap: wrap;
  }

  .navbar-expand-lg {
    flex-wrap: wrap;
  }

  .navbar-collapse {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;

    background-color: $basic-light-blue;
    border-radius: 20px;
  }

  .site-menu_item {
    margin-bottom: 20px;
  }

  .site-menu_list {
    margin-top: 30px;
    margin-left: 80px;
  }

  .phones {
    @include reset-list;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    margin-top: 10px;
    margin-bottom: 30px;
    margin-left: 0;
    padding-left: 20px;
  }

  .user-nav_item {
    &:first-child {
      margin-right: 30px;
    }
  }

  .site-menu_list,
  .user-nav {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .site-menu_list {
    margin-left: 40px;
  }

  .user-nav {
    padding-left: 20px;
  }
}
