.form {
  padding: 70px;

  background-color: $dark-blue;
}

.form__title {
  font-size: 40px;
  font-weight: 800;
  text-align: center;
  color: $basic-white;
  text-transform: uppercase;
}

.form__subtitle {
  font-size: 20px;
  text-align: center;
  color: $basic-white;

  & span {
    text-transform: uppercase;
  }
}

.form--wrapper {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.form__input--phone {
  width: 300px;
  padding: 20px 20px;
  padding-left: 70px;

  background-color: $basic-white;
  background-image: url("../img/icon-phone-grey.png");
  background-repeat: no-repeat;
  background-position: 10px center;
  border: none;
  border-radius: 40px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.15) inset;


  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.9;
  }
}

.form__input--btn {
  width: 300px;
  margin-left: 20px;
  padding: 20px 20px;

  color: $basic-white;

  background-color: $special-blue;
  background-repeat: no-repeat;
  background-position: 10px center;
  border: none;
  border-radius: 40px;

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.9;
  }
}

.form__text {
  margin-top: 40px;

  font-size: 18px;
  text-align: center;
  color: $basic-white;
}

@media screen and (max-width: 767px) {
  .form--wrapper {
    flex-direction: column;
    align-items: center;
  }

  .form__input--btn {
    margin-left: 0;
    margin-top: 30px;
  }
}

@media screen and (max-width: 550px) {
  .form__title {
    font-size: 30px;
  }
}
