.page-footer {
  margin-top: 50px;
  padding: 20px;

  background-color: $special-grey;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__email {
  text-align: center;
}

.page-footer__text {
  margin-bottom: 7px;

  font-size: 14px;
  color: $basic-white;
}

.page-footer__email {
  margin-bottom: 0;
  padding-left: 35px;

  font-size: 16px;
  color: $basic-white;

  background-image: url("../img/icon-email.png");
  background-repeat: no-repeat;
  background-position: left center;
}

.footer-phones {
  @include reset-list;
}

.footer-phones_item {
  &:last-child {
    margin-top: 5px;
  }
}

.footer-phones_link {
  font-weight: 600;
  color: $basic-white;
  text-decoration: none;

  &:hover {
    color: $basic-grey;

    opacity: 0.6;
  }

  &:focus {
    outline: none;
    opacity: 0.6;
  }
}

@media screen and (max-width: 991px) {
  .footer {
    flex-wrap: wrap;
  }

  .footer-phones {
    margin-top: 20px;
  }
}

@media screen and (max-width: 500px) {
  .footer {
    flex-direction: column;
    align-items: center;
  }

  .footer-phones {
    margin-top: 30px;
  }

  .footer__email {
    margin-top: 30px;
  }
}
