.main-screen {
  padding: 20px 0 0;

  background-color: $basic-light-blue;
}


.main-screen__title {
  font-weight: 800;
  font-size: 50px;
  color: $dark-blue;
}

.main-screen--wrapper {
  display: flex;
  align-items: center;
}

.main-screen__subtitle {
  font-size: 22px;
  color: $basic-grey;
}

.main-screen__btn {
  display: block;
  width: 300px;
  margin-top: 80px;
  padding: 15px 20px;
  padding-right: 50px;

  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  color: $basic-white;
  text-decoration: none;

  background-color: $basic-orange;
  background-image: url("../img/icon-phone.png");
  background-repeat: no-repeat;
  background-position: 240px center;
  border-radius: 40px;

  &:hover {
    color: $basic-white;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

@media screen and (max-width: 991px) {
  .main-screen {
    padding: 100px 0 0;
  }

  .main-screen__btn {
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 550px) {
  .main-screen__title {
    font-size: 40px;
  }

  .main-screen__subtitle {
    font-size: 18px;
  }
}

@media screen and (max-width: 430px) {
  .main-screen__title {
    font-size: 32px;
  }
}

@media screen and (max-width: 380px) {
  .main-screen__title {
    font-size: 28px;
  }
}
